import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import api from "../../utils/api";
import Navbar from "../../components/Navbar";

export default function Dashboard() {
  const [user, setUser] = useState(null);
  const [form, setForm] = useState({ name: "", email: "" });
  const router = useRouter();

  // âœ… Load user info when dashboard loads
  const loadUser = async () => {
    try {
      const res = await api.get("/auth/me");
      setUser(res.data);
      setForm({ name: res.data.name, email: res.data.email });
    } catch (err) {
      console.error("Load user failed:", err);
      // à¦¯à¦¦à¦¿ token à¦¨à¦¾ à¦¥à¦¾à¦•à§‡ à¦¬à¦¾ expire à¦¹à§Ÿà§‡ à¦¯à¦¾à§Ÿ â†’ login à¦ªà§‡à¦œà§‡ à¦ªà¦¾à¦ à¦¾à¦“
      router.push("/login");
    }
  };

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      router.push("/login");
    } else {
      loadUser();
    }
  }, []);

  if (!user)
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading user info...</p>
      </div>
    );

  return (
    <div className="min-h-screen bg-gray-100">
      <Navbar />
      <div className="max-w-2xl mx-auto bg-white mt-10 p-6 rounded-2xl shadow-lg">
        <h2 className="text-2xl font-semibold mb-4">Welcome, {user.name} ðŸ‘‹</h2>
        <div className="space-y-2">
          <p>
            <strong>Name:</strong> {user.name}
          </p>
          <p>
            <strong>Email:</strong> {user.email}
          </p>
        </div>

        <button
          onClick={() => {
            localStorage.removeItem("token");
            router.push("/login");
          }}
          className="mt-6 bg-red-500 text-white py-2 px-4 rounded hover:bg-red-600"
        >
          Logout
        </button>
      </div>
    </div>
  );
}
